
:root {
  --primary-color: #e63946; /* red */
  --secondary-color: #a8dadc; /* light blue-green */
  --tertiary-color: #457b9d; /* dark blue */
  --text-color: #1d3557; /* dark blue */
  --background-color: #f1faee; /* off-white */
}

/* Global Styles */

body {
  background-color: var(--background-color);
  font-family: sans-serif;
  color: var(--text-color);
}

/* Form Styles */

.form {
  max-width: 500px;
  margin: 0 auto;
  padding: 1rem;
  border-radius: 8px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  background-color: white;
}

.label {
  display: block;
  margin-bottom: 0.5rem;
  color: var(--primary-color);
}

.input {
  display: block;
  width: 95%;
  padding: 0.5rem;
  margin-bottom: 1rem;
  border: 2px solid var(--secondary-color);
  border-radius: 4px;
  font-size: 1rem;
  color: var(--text-color);
}

.btn {
  display: block;
  width: 100%;
  padding: 0.5rem;
  margin-top: 1rem;
  background-color: var(--primary-color);
  color: white;
  font-size: 1rem;
  font-weight: bold;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.success {
  margin-top: 1rem;
  padding: 0.5rem;
  background-color: var(--tertiary-color);
  color: white;
  border-radius: 4px;
}

.error {
  margin-top: 1rem;
  padding: 0.5rem;
  background-color: #ff0000; /* red */
  color: white;
  border-radius: 4px;
}

/* Adjusted styles for checkboxes */

.checkbox-group {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 1rem;
}

.checkbox-label {
  margin-right: 0.5rem;
  color: var(--primary-color);
}

.checkbox {
  margin-right: 0.5rem;
}

.checkbox:checked + .checkbox-label {
  color: var(--text-color);
}